:root {
  --dark-text: #1f2329;
  --primary-blue: #2f73da;
}

body {
  font-family: "Montserrat", sans-serif;
  background-color: #fbfcfd;
}

h1 {
  font-size: 2rem;
  line-height: 2.5rem;
  font-weight: 700;
  margin: 0;
  color: var(--dark-text);
}

h2 {
  font-size: 1.25rem;
  line-height: 2rem;
  font-weight: 700;
  color: var(--dark-text);
}

h3 {
  font-size: 1rem;
  line-height: 1.5rem;
  font-weight: 700;
  margin: 0;
  color: var(--dark-text);
}

p {
  font-size: 1rem;
  line-height: 1.5;
  font-weight: 400;
  color: var(--dark-text);
  margin: 0;
}

.btn {
  font-size: 1rem;
  line-height: 1.25rem;
  font-weight: 500;
  padding: 10px 16px;
}

button.btn.btn-primary {
  background-color: var(--primary-blue);
  border: 1px solid var(--primary-blue);
}

button.btn.btn-primary:hover {
  background-color: #034ab3;
  border: 1px solid #034ab3;
}

button.btn.btn-outline-primary {
  background-color: #ffffff;
  color: var(--primary-blue);
  border: 1px solid #cdd3dd;
}

button.btn.btn-outline-primary:hover {
  background-color: #2f73da;
  color: #fff;
  border: 1px solid #2f73da;
}

.reservation-container,
.form-container {
  background-color: #f8f9fb;
  border: 1px solid #eef1f6;
  border-radius: 0.5rem;
  box-shadow: 0px 2px 8px 0px rgba(106, 115, 129, 0.12);
}

.table-card {
  border: 1px solid #eaf1fb;
}

.table-filled {
  background-color: #eaf1fb;
}
